@import '../../styles/mixins.scss';
@import '../../styles/variables.scss';
.Search {
  display: flex;
  flex-direction: column;
  @include width(160);
  @include margin(2);
  @include padding(1);
  margin: auto;
  .wrapper {
    display: flex;
    align-items: center;
    @include margin(1);
    .search-field {
      flex: 1;
      @include padding(1);
      font-size: 1rem;
      &:focus {
        outline: none;
      }
    }
  }
  .header-box {
    flex: 1;
    @include padding(1);
    color: $accent-color-dark;
    .header-title {
      @include contentDetailTitle();
    }
    .header-body {
      @include maxWidth(55);
      @include contentDetailBody();
    }
  }
}

@media screen and (max-width: 1634px) {
  .Search {
    @include width(128);
  }
}

@media screen and (max-width: 1334px) {
  .Search {
    @include width(96);
  }
}

@media screen and (max-width: 1014px) {
  .Search {
    @include width(64);
  }
}

@media screen and (max-width: 694px) {
  .Search {
    @include width(32);
  }
  .wrapper {
    flex-direction: column;
    .search-field {
      align-self: stretch;
    }
  }
}
