//Layout, Box, Visual, Typography, Misc
@import '../../styles/mixins.scss';
@import '../../styles/variables.scss';
.Header {
  @include padding(1.5);
  border-bottom: $simple-border;
  background-color: $snow;
  .wrapper {
    display: flex;
    justify-content: space-between;
    align-items: middle;
    @include width(160);
    margin: auto;
    .headerLogo {
      flex: 1;
      @include height(4.5);
      @include width(30);
      background: {
        image: url('../../images/branding/Our-Planet-Wordmark-Small.png');
        repeat: no-repeat;
        size: contain;
      }
    }
    .nav-bar {
      display: flex;
      align-items: center;
      .nav-button {
        @include width(10);
        @include button();
        &:hover {
          @include buttonHover();
        }
      }
      .active {
        @include activeButton();
        &:hover {
          background-color: $accent-color-dark;
        }
      }
    }
  }
}

@media screen and (max-width: 1634px) {
  .Header {
    .wrapper {
      @include width(128);
    }
  }
}

@media screen and (max-width: 1334px) {
  .Header {
    .wrapper {
      @include width(96);
    }
  }
}

@media screen and (max-width: 1014px) {
  .Header {
    .wrapper {
      flex-direction: column;
      align-items: center;
      @include width(64);
      .headerLogo {
        margin-bottom: 15px;
      }
    }
  }
}

@media screen and (max-width: 694px) {
  .Header {
    .wrapper {
      @include width(32);
    }
  }
}
